export default {
	data() {
		return {
			login: false,
			avatarUrl: '/static/logo.png',
			uerInfo: {}
		}
	},
	methods: {
		goLogin() {
			if (!this.login) {
				uni.navigateTo({
					url: '/pages/login/login'
				});
			}
		},
		goAbout() {
			uni.navigateTo({
				url: '/pages/about/about'
			});
		}
	}
}
